    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 24px;

    .article {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        font-weight: 400;
        position: relative;

        &-text {
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 100%;
            margin: 10px 0;

            svg {
                transition: transform 0.1s ease-in;
            }

            svg:hover {
                transform: scale(1.2);
            }
        }

        .photo-box {
            width: 100%;
            transition: transform 0.1s ease-in;

            .photo {
                width: 100%;
            }

            .like {
                position: absolute;
